.ListInput {

}

.ListInput > div {
  display: inline-flex;
  width: calc(100% - 25px);
  align-items: center;
  height: 100%;
}

.ListInput input {
  outline: none;
  font-size: 1.1rem;
  display: inline;
  width: 100%;
}

@media (max-width: 600px) {
  .ListInput input {
    padding: 5px 0 5px 5px;
  }
}

.PositionButtons {
  display: flex;
  height: 100%;
}

.MethodList > div {
  padding-left: 8px;
  width: calc(100% - 17px);
}